<template>
  <van-list
    class="article_wrap"
    v-model="loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onCommentLoad"
  >
    <van-cell :title="title"></van-cell>
    <comment-item
      v-for="(comment, index) in list"
      :key="index"
      :comment="comment"
      @onReplay="$emit(`onReplay`, $event)"
    ></comment-item>
  </van-list>
</template>

<script>
import { getComments } from '@/api/article.js';

export default {
  name: 'CommentList',
  props: {
    source: {
      type: [String, Number, Object],
      required: true,
    },
    list: {
      type: Array,
      default() {
        return [];
      },
    },
    title: {
      type: String,
    },
    type: {
      type: String,
      default: 'a',
    },
  },
  components: {
    CommentItem: () => import('./comment_item.vue'),
  },
  data() {
    return {
      loading: false, // 评论是否在加载
      limit: 10, // 每次获取几条
      finished: false, // 是否全部加载完了
      offset: null, // 获取评论数据的偏移量，值为评论id，表示从此id的数据向后取，不传表示从第一页开始读取数据
    };
  },
  methods: {
    // 加载评论或者评论回复
    async onCommentLoad() {
      try {
        const { data } = await getComments({
          type: this.type, //
          // source: 1,
          source: this.source.toString(), // 文章id或评论id
          offset: this.offset,
          limit: this.limit,
        });

        this.$emit('input', data.data.total_count); // 评论总数 传值父组件
        // this.$emit(`update_totalCount`, data.data.total_count);
        const { results, last_id } = data.data;
        this.list.push(...results);
        this.loading = false;
        if (results.length) {
          this.offset = last_id;
        } else {
          this.finished = true;
        }
      } catch (e) {
        console.log(e);
      }
      this.finished = true;
    },
  },
  watch: {},
  created() {},
};
</script>

<style lang="less" scoped>
.article_wrap {
  padding-top: 15px;
  background-color: #fff;
}
</style>
